.TodoLoading {
    display: flex;
    flex-direction: column-reverse;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: -10px;
    right: -10px;
    bottom: -10px;
    left: -10px;
    background: rgb(2, 1, 1);
    z-index: 10;
}

.TodoLoadingImage {
    width: 200px;
    height: 200px;
    object-fit: contain;
    animation: rotate 5s infinite ease;
}

.TodoLoadingText {
    font-size: 2rem;
    margin: 0 0 3rem;
}

.TodoLoadingText span {
    color: rgb(0, 143, 253);
}

@keyframes rotate {
    0% {
        transform: rotate(-360deg);
        scale: (1);
    }

    100% {
        transform: rotate(360deg);
        scale: (1.5);
    }
}

@media (max-width: 600px) {
    
.TodoLoadingText {
    font-size: 1.5rem;
}
}