.card-main{
  background: linear-gradient(0deg,rgba(0,0,0,0.712),rgba(0,0,0,0.712)),url('../Img/z6btu437zgi91.webp') center center ;
  background-size: cover;
  border-radius: 3rem;
  box-shadow: .5rem .5rem 10px #030303f3;
  border: 2px solid #c8c8c8f1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 1rem 0;
  width: 45vw;
  height: 90vh;
}



@media (max-width:991px) {
  .card-main{
    width: 100vw;
    text-align: center;
  }
}


