.TodoItem{
    background-color: rgb(2, 1, 1);
    border: 1px solid #f2f2f2;
    border-radius: 1rem;
    box-shadow: 2px 2px 10px rgb(2, 1, 1);
    display: flex;
    justify-content: space-between;
    font-size: 1.1rem;

    margin: 1rem 0;
    padding: .8rem;
    width: 30vw;
}

.TodoItem p {
    overflow-x: hidden;
}

.CompletedTask{
    cursor: pointer;
    color: #f2f2f2;
    transition:500ms transform ease-in-out;
}

.CompletedTask:hover{
    color: greenyellow;
    transform: scale(1.2);
}

.CompletedTask--complete{
    color: greenyellow;
}

.DeletedTask{
    cursor: pointer;
    color: #f2f2f2;
    transition:500ms transform ease-in-out;
}

.DeletedTask:hover{
    color: #c50808;
    transform: scale(1.2);
}

.checkTask{
    text-decoration: line-through;
}


@media (max-width: 991px){
    .TodoItem{
        width: 60vw;
    }
}