

.TodoError{
    display: flex;
    flex-direction: column-reverse;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: -10px;
    right: -10px;
    bottom: -10px;
    left: -10px;
    background:  linear-gradient(0deg,rgb(0, 0, 0),rgb(0, 0, 0));
    z-index: 10;
}

.TodoErrorImage{
    width:  300px;
    height: 300px;
    object-fit:cover;
}

.TodoErrorText{
    font-size: 1.5rem;
    margin: 0 0 1rem;
}
.TodoErrorText span{
    color: yellowgreen;
}

@media (max-width: 600px) {
    .TodoErrorText{
        font-size: 1.5rem;
    }
}