.TodoForm{
    background: rgb(2, 1, 1);
    box-shadow: 5px 5px 10px rgb(2, 1, 1);
    border: 1px solid #f2f2f2;
    border-radius: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    width: 30vw;
    height: 40vh;
    padding: 1rem;
}

.TodoLabel{
    color: #f2f2f2f2;
    font-weight: bold;
    padding: 1rem 0;
}

.TodoTextArea{
    background: rgb(2, 1, 1);
    border: 1px solid #f2f2f2;
    border-radius: 1rem;
    color: #f2f2f2;
    padding: 1rem;
    font-family: 'Open Sans', sans-serif;
    text-align: center;
    resize: none;
    height: 50%;
    width: 50%;
}
.TodoTextArea::placeholder{
    font-family:'Open Sans', sans-serif ;
    
}

.ContainerFormButtons{
    display: flex;
    justify-content: center;
    padding: 1rem;
}

.ContainerFormButtons button{
    background-color: rgb(2, 1, 1);
    border: 1px solid #f2f2f2;
    border-radius: 1rem;
    color: #f2f2f2;
    font-family:'Open Sans', sans-serif ;
    font-weight: bold;
    
    padding: .6rem .6rem;
    margin: 0 1rem;
}

button[type='submit']:hover{
    color: greenyellow;
}
button[type='button']:hover{
    color: #c50808;
}

@media (max-width:991px) {
    .TodoForm{
        width: 80vw;
        height: 60vh;
        padding: 1rem;
    }
    .TodoLabel{
        text-align: center;
    }
    .TodoTextArea{
        width: 65%;
    }
}